.app {
  display: table;
  position: absolute;
  height: 100%;
  width: 100%;
  background: -webkit-radial-gradient(circle farthest-side, #fceabb6e, #f8b500);
  background: -o-radial-gradient(circle farthest-side, #fceabb6e, #f8b500);
  background: radial-gradient(circle farthest-side, #fceabb6e, #f8b500);
  background-repeat: no-repeat; }

.app-screen {
  display: table-cell;
  vertical-align: middle; }

.app-content {
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
  padding: 0 15px; }

.banner {
  height: 180px;
  background-size: cover;
  background-position: 50%; }



